export default {
	created() {
		var neirong = this.questionList[this.number-1]
		this.content = neirong.content
	},
	data() {
		return {
			content: '',
			isActive: ''
		}
	},
	methods: {
		changeActive(e){
			this.isActive = e
		},
		changeQuestion(e){
			var data ={}
			if(this.nextText == "提交"){
				data = {
					ps: e,
					number: this.number-1,
					answer: this.isActive,
					submit: true,
					tixing: this.questionList[this.number-1].tixing
				}
			}else{
				data = {
					ps: e,
					number: this.number-1,
					answer: this.isActive,
					submit: false,
					tixing: this.questionList[this.number-1].tixing
				}
			}	
			this.$emit('func', data);
		}
	},
	props:[
		"number",
		"questionList",
		"nextText"
	]
}
